const url = 'api/posts';

class PostService {

    //Read Post

    static async getPosts() {
        return await fetch(url ,{
            method: 'GET'
        })
        .then(r => r.json())
        .then(json => {return {
                success: true,
                data: json.map(post => {
                    return {
                        ...post,
                        createdAt:  new Date(post.createdAt)
                    }
            })
            }})
        .catch(err => {
            return {
                success: false,
                data: err
            }
        });
    };

    static async createPost(text) {
        return await fetch(url, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                text
            })
        })
        .then(r => r.json())
        .then(json => json)
        .catch(err => err);
    };
    static async deletePost(id) {
        return await fetch(url+'/'+id, {
            method: 'DELETE',
        });
    }
}

export default PostService;