<template>
  <div class="container">
    <h1>Latest Posts</h1>
    <!-- Create Posts Here -->
    <div class="create-post">
      <label for="create-post">Say something ...</label>
      <input type="text" id="create-post" v-model="text" placeholder="Create a post">
      <button v-on:click="createPost">Post!</button>
    </div>
    <hr>
    <p class="error-message" v-if='error'> {{error}} </p>
    <div class="post-container">
      <div 
      class="post" 
      v-for="(post,index) in posts"
      v-bind:item="post"
      v-bind:index="index"
      v-bind:key="post._id"
      v-on:dblclick="deletePost(post._id)"
      >
        {{`${post.createdAt.getDate()}/${post.createdAt.getMonth()}`}}
        <div class="text">
          {{post.text}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PostService from '../PostService'

export default {
  name: 'PostComponent',
  data() {
    return {
      posts: [],
      error: '',
      text: ''
    }
  },
  async created() {
    this.readPosts()
      
  },

  methods: {
    async readPosts() {
            let data = await PostService.getPosts();
      if (data.success)
      this.posts = data.data
      else
      this.error = data.data
    },
    async createPost() {
      await PostService.createPost(this.text);
      this.readPosts();
    },
    async deletePost(id) {
      await PostService.deletePost(id);
      this.readPosts();
    }
  },
}
</script>

